<template>
  <div>
    <mt-swipe :auto="0" :speed='500' :continuous="false" :defaultIndex='0' :showIndicators="false" class='swiper_bg' v-show="hasData">
      <mt-swipe-item v-for="(item,index) in headBg" :key='index'>
        <img :src="item.image_pc" class='swiper_bg_img' @click="imgLink(item)">
      </mt-swipe-item>
    </mt-swipe>
  </div>
</template>

<script>
import img1 from '@/assets/img/guide/1.png'
import img2 from '@/assets/img/guide/2.png'
import img3 from '@/assets/img/guide/3.png'
import {
  getAdvImg
} from '@/api/index'
import {
  InfiniteScroll,
  Indicator,
  Swipe,
  SwipeItem,
  Lazyload,
  Toast
} from 'mint-ui'
export default {
  name: 'NavigationPage',
  components: {
    Swipe,
    SwipeItem,
    Lazyload,
    Toast,
    InfiniteScroll,
    Indicator
  },
  data() {
    return {
      hasData: false,
      headBg: [{
        image_pc: img1,
        type: 0,
        link: ''
      },
      {
        image_pc: img2,
        type: 0,
        link: ''
      },
      {
        image_pc: img3,
        type: 1,
        link: '/index'
      }
      ]
    }
  },
  mounted() {
    console.log('引导页')
    var storage = window.localStorage
    console.log('是否显示引导页' + storage.getItem('showGuide'))
    if (storage.getItem('showGuide') == 0) {
      this.hasData = true
    } else {
      let that = this
      getAdvImg().then(res => {
        if (res.isSuccess) {
          console.log(res)
          if (res.data.length > 0) {
            // 跳转广告页
            that.$router.replace({
              path: '/guidePage?img=' + res.data[0]
            })
          } else {
            that.$router.replace({
              path: '/index'
            })
          }
        }
      })
    }
  },
  methods: {
    imgLink(data) {
      if (data.type == 1) {
        var storage = window.localStorage
        storage.setItem('showGuide', 1)
        console.log('是否显示引导页' + storage.getItem('showGuide'))
        this.$router.replace({
          path: data.link
        })
      }
    }
  }
}

</script>
<style lang="less" scoped>
  .swiper_bg {
    height: 100%;
    background: #fff;
  }

  .swiper_bg_img {
    width: 7.5rem;
  }

</style>



